.App {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  color: white;
}

.container {
  display: flex;
  height: 80vh;
  border: 1px solid gray;
  padding: 20px;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
}

.toDoForm {
  display: flex;
  justify-content: space-around;
  width: 450px;
}

input {
  width: 80%;
  padding: 10px;
  border-radius: 20px;
  border: none;
}

button {
  border: none;
  padding: 10px;
  border-radius: 5px;
  margin: 0 5px;
}

.allToDo {
  list-style: none;
  margin: 0;
  padding: 0;
}

.singleToDo {
  border-radius: 10px;
  margin: 10px 0;
  background-color: #5dadf8;
  padding: 10px 20px;
  display: flex;
  width: 450px;
  align-items: center;
}

.toDoText {
  flex: 1;
}
